<template>
  <div class="resourceConsolidation" ref="resourceConsolidation">
    <ul class="rc-ul-1">
      <li class="rc-li-1" v-for="(row, rowIndex) in mockData" :key="rowIndex">
        <ul class="rc-ul-2">
          <li
            class="rc-li-2"
            draggable="true"
            :data-id="`${rowIndex}-${colIndex}`"
            v-for="(col, colIndex) in row"
            :key="colIndex"
          >
            <div class="rc-handle">
              <i class="css-icon icon-menu"></i>
            </div>
            <h3 class="rc-name">{{ col.name }}</h3>
            <a-icon
              v-if="colIndex === 0"
              type="plus"
              @click="showModal(row, rowIndex)"
            />
            <a-icon
              v-else
              type="close"
              @click="remove(row, rowIndex, col, colIndex)"
            />
          </li>
        </ul>
      </li>
    </ul>

    <template v-if="visible">
      <dialog-add :visible.sync="visible" :row="rowTarget" @ok="handleOk" />
    </template>
  </div>
</template>

<script>
/* eslint-disable */
import { getMockData_list, getParentElement, getIndexArray } from './tool'
import DialogAdd from './DialogAdd'
export default {
  name: 'ResourceConsolidation',
  components: {
    DialogAdd,
  },
  data() {
    return {
      mockData: getMockData_list(20),
      visible: false,
      rowTarget: {},
    }
  },
  mounted() {
    this.initEvent()
  },
  beforeDestroy() {
    this.destroy()
  },
  methods: {
    showModal(row, rowIndex) {
      this.visible = true
      this.rowTarget = row
      this.rowIndex = rowIndex
    },
    handleOk(checked) {
      const len1 = checked.length
      const { mockData } = this
      for (let h = 0; h < len1; h++) {
        const check = checked[h]
        const rowLen = mockData.length
        for (let i = 0; i < rowLen; i++) {
          const row = mockData[i]
          const colLen = row.length
          let removedItem = null
          for (let j = 0; j < colLen; j++) {
            const col = row[j]
            if (check.name === col.name) {
              removedItem = row.splice(j, 1)[0]
              mockData[this.rowIndex].push(removedItem)
              break
            }
          }
          if (!row.length) {
            mockData.splice(i, 1)
            if (this.rowIndex > i) {
              this.rowIndex--
            }
          }
          if (removedItem) {
            break
          }
        }
      }
    },
    remove(row, rowIndex, col, colIndex) {
      this.$confirm({
        title: '取消合并确认',
        content: '确认取消该条目的自动合并设置？',
        onOk: () => {
          row.splice(colIndex, 1)
          this.mockData.splice(rowIndex + 1, 0, [col])
        },
        cancelText: '取消',
        okText: '确认',
      })
    },
    dragstart(event) {
      const { target, dataTransfer } = event
      if (!target || target.nodeType !== 1) {
        event.preventDefault()
        return
      }
      this.pos_origin = getIndexArray(target)
      this.dragged = target
      dataTransfer.effectAllowed = 'move'
    },
    dragover(event) {
      if (!this.dragged) return
      const { target } = event
      if (!this.$refs.resourceConsolidation.contains(target)) return
      const dropzone = getParentElement(target, 'rc-li-2')
      if (this.dragged !== dropzone && dropzone.className.includes('rc-li-2')) {
        event.preventDefault()
        this.pos_target = getIndexArray(dropzone)
        dropzone.style.cursor = "move"
      }
    },
    drop(event) {
      if (!this.dragged) return
      event.preventDefault()
      this.move()
    },
    move() {
      const { pos_origin, pos_target } = this
      const [pos_origin_rowIndex, pos_origin_colIndex] = pos_origin
      const [pos_target_rowIndex, pos_target_colIndex] = pos_target
      const [removedItem] = this.mockData[pos_origin_rowIndex].splice(
        pos_origin_colIndex,
        1
      )
      this.mockData[pos_target_rowIndex].splice(
        pos_target_colIndex + 1,
        0,
        removedItem
      )
      if (this.mockData[pos_origin_rowIndex].length === 0) {
        this.mockData.splice(pos_origin_rowIndex, 1)
      }
    },
    keydown(event) {
      const { defaultPrevented, key } = event
      if (defaultPrevented) return
      switch (key) {
        case 'x':
          this.cut()
          break
        case 'v':
          this.paste()
          break
        default:
          return
      }
      event.preventDefault()
    },
    click(event) {
      const { target } = event
      if (typeof target.className !== 'string') return
      if (!this.$refs.resourceConsolidation.contains(target)) return
      this.ele_o && this.ele_o.classList.remove('vm-select')
      const ele_o = (this.ele_o = getParentElement(target, 'rc-li-2'))
      if (ele_o.className.includes('rc-li-2')) {
        ele_o.classList.add('vm-select')
      } else {
        this.ele_o = null
      }
    },
    cut() {
      if (!this.ele_o) return
      this.removeT()
      this.ele_t = this.ele_o
      this.removeO()
      this.ele_t.classList.add('vm-active')
    },
    paste() {
      const copyedEle = document.querySelector('.vm-active')
      const pastedEle = document.querySelector('.vm-select')
      this.pos_origin = getIndexArray(copyedEle)
      this.pos_target = getIndexArray(pastedEle)
      this.move()
      this.removeO()
      this.removeT()
    },
    dblclick(event) {
      const { target } = event
      if (!this.$refs.resourceConsolidation.contains(target)) {
        this.removeO()
        this.removeT()
      }
    },
    removeT() {
      this.ele_t && this.ele_t.classList.remove('vm-active')
      this.ele_t = null
    },
    removeO() {
      this.ele_o && this.ele_o.classList.remove('vm-select')
      this.ele_o = null
    },
    initEvent() {
      document.addEventListener('dragstart', this.dragstart, false)
      document.addEventListener('dragover', this.dragover, false)
      document.addEventListener('drop', this.drop, false)
      document.addEventListener('keydown', this.keydown, true)
      document.addEventListener('click', this.click, false)
      document.addEventListener('dblclick', this.dblclick, false)
    },
    destroy() {
      document.removeEventListener('dragstart', this.dragstart, false)
      document.removeEventListener('dragover', this.dragover, false)
      document.removeEventListener('drop', this.drop, false)
      document.removeEventListener('keydown', this.keydown, true)
      document.removeEventListener('click', this.click, false)
      document.removeEventListener('dblclick', this.dblclick, false)
    },
  },
}
</script>

<style lang="less" src="./resourceConsolidation.less"></style>