<template>
  <div class="resourceConsolidationIndex">
    <div class="rci-header">
      <a-breadcrumb>
        <a-breadcrumb-item>资源合并</a-breadcrumb-item>
        <a-breadcrumb-item>resource consolidation</a-breadcrumb-item>
      </a-breadcrumb>
      <div>
        <a-input-search
          placeholder="搜索"
          style="width: 200px"
          @change="change"
        />
      </div>
    </div>
    <div class="rci-body">
      <a-tabs default-active-key="1">
        <a-tab-pane key="1">
          <span slot="tab">
            <a-icon type="apple" />
            Mule
          </span>
          <resource-consolidation></resource-consolidation>
        </a-tab-pane>
        <a-tab-pane key="2">
          <span slot="tab">
            <a-icon type="android" />
            SIMU
          </span>
          SIMU
        </a-tab-pane>
        <a-tab-pane key="3">
          <span slot="tab">
            <a-icon type="android" />
            SIMU+
          </span>
          SIMU+
        </a-tab-pane>
        <a-tab-pane key="4">
          <span slot="tab">
            <a-icon type="android" />
            EP1
          </span>
          EP1
        </a-tab-pane>
        <a-tab-pane key="5">
          <span slot="tab">
            <a-icon type="android" />
            EP2
          </span>
          EP2
        </a-tab-pane>
        <a-tab-pane key="6">
          <span slot="tab">
            <a-icon type="android" />
            PPV
          </span>
          PPV
        </a-tab-pane>
        <a-tab-pane key="7">
          <span slot="tab">
            <a-icon type="android" />
            PP
          </span>
          PP
        </a-tab-pane>
        <a-tab-pane key="8">
          <span slot="tab">
            <a-icon type="android" />
            P
          </span>
          P
        </a-tab-pane>
      </a-tabs>
    </div>
  </div>
</template>

<script>
import ResourceConsolidation from './ResourceConsolidation.vue'
export default {
  name: 'ResourceConsolidationIndex',
  components: {
    ResourceConsolidation,
  },
  methods: {
    change(e) {
      const { value } = e.target
      this.timer && clearTimeout(this.timer)
      this.timer = setTimeout(() => {
        const eles = document.querySelectorAll('.rc-name')
        const { length } = eles
        let first = null
        for (let i = 0; i < length; i++) {
          const ele = eles[i]
          const { textContent } = ele
          if (value && textContent.includes(value)) {
            if (!first) {
              first = ele
            }
            ele.classList.add('rc-highlight')
          } else {
            ele.classList.remove('rc-highlight')
          }
        }
        first &&
          first.scrollIntoView({
            behavior: 'smooth',
            block: 'start',
            inline: 'nearest',
          })
      }, 500)
    },
  },
}
</script>

<style lang="less">
.resourceConsolidationIndex {
  background-color: #f5f5f5;
  padding: 15px;
  .rci-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
  }
  .rci-body {
    padding: 15px;
    background-color: white;
    border-radius: 10px;
  }
  .ant-btn {
    margin-left: 5px;
  }
  .rc-highlight {
    color: red;
  }
}
</style>