export { getParentElement } from '../vehicleMerger/tool'

export const getMockData_list = (max) => {
  let arr = []
  for (let i = 0; i < max; i++) {
    const serialNumber = (i + 1).toString().padStart(3, '0')
    arr.push([
      {
        name: `二级条目${serialNumber}`,
      }
    ])
  }
  return arr
}

export const getMockData_dialog = (level1 = 42, level2 = 10) => {
  let obj = {
    name: '试验条目',
    children: []
  }
  const children1 = obj.children
  for (let i = 0, k = 0; i < level1; i++) {
    const serialNumber = (i + 1).toString().padStart(3, '0')
    children1.push(
      {
        name: `一级条目${serialNumber}`,
        unfolded: true,
        children: [],
      }
    )
    const children2 = children1[i].children
    for (let j = 0; j < level2; j++) {
      const serialNumber = (k + 1).toString().padStart(3, '0')
      children2.push(
        {
          name: `二级条目${serialNumber}`,
        }
      )
      k++
    }
  }
  return obj
}

export const getIndexArray = (target) => {
  let [rowIndex, colIndex] = target.dataset.id.split('-')
  rowIndex *= 1
  colIndex *= 1
  return [rowIndex, colIndex]
}

export const flatTarget = (item) => {
  let { related } = item
  if (!related) {
    related = []
  }
  return [item, ...related]
}

export const removeRepeatItem = (mockData, item_target) => {
  const level1 = mockData.children
  const len1 = level1.length
  for (let i = 0; i < len1; i++) {
    const level2 = level1[i].children
    const len2 = level2.length
    let removedItem = null
    for (let j = 0; j < len2; j++) {
      const item = level2[j]
      if (item.name === item_target.name) {
        removedItem = level2.splice(j, 1)
        break
      }
    }
    if (level2.length === 0) {
      level1.splice(i, 1)
      break
    }
    if (removedItem) {
      break
    }
  }
}