<template>
  <vxe-modal
    className="rDialog-add"
    width="600"
    height="80%"
    title="新增可合并条目"
    show-footer
    type="confirm"
    :value="visible"
    :destroy-on-close="true"
    @confirm="handleOk"
    @cancel="handleCancel"
    @close="handleCancel"
  >
    <h4 class="rd-h4">请选择可以与<em>智能座舱软件功能开发</em>合并的条目：</h4>
    <div class="rd-selectArea">
      <a-input placeholder="搜索条目" @change="onSearch" />
      <template v-if="mockData.children.length > 0">
        <h5 class="rd-h5">{{ mockData.name }}</h5>
        <ul class="rd-ul-1">
          <li
            class="rd-li-1"
            v-for="(item2, index2) in mockData.children"
            :key="index2"
          >
            <h6 class="rd-h6" @click="toggle(item2)">
              <a-icon v-if="item2.unfolded" type="minus-circle" theme="filled" />
              <a-icon v-else type="plus-circle" theme="filled" />
              <span>{{ item2.name }}</span>
            </h6>
            <ul class="rd-ul-2" v-if="item2.unfolded">
              <li
                class="rd-li-2"
                v-for="(item3, index3) in item2.children"
                :key="index3"
              >
                <span>{{ item3.name }}</span>
                <a-checkbox @change="onChange(item3)" />
              </li>
            </ul>
          </li>
        </ul>
      </template>
      <p class="rd-empty" v-else>暂无可合并条目</p>
    </div>
  </vxe-modal>
</template>

<script>
/* eslint-disable */
import { removeRepeatItem, getMockData_dialog } from './tool'
let checked = []
export default {
  name: 'DialogAdd',
  props: {
    visible: {
      type: Boolean,
      default: false,
    },
    row: {
      type: Array,
      default() {
        return []
      },
    },
  },
  data() {
    return {
      mockData: getMockData_dialog(2, 10),
    }
  },
  created() {
    this.mockDataCopy = JSON.parse(JSON.stringify(this.mockData))
    this.filteredMockData()
  },
  methods: {
    filteredMockData() {
      const { mockData, row } = this
      const mockDataCopy = JSON.parse(JSON.stringify(mockData))
      const len_target = row.length
      for (let i = 0; i < len_target; i++) {
        const item_target = row[i]
        removeRepeatItem(mockDataCopy, item_target)
      }
      this.mockData = mockDataCopy
    },
    onSearch(e) {
      const value = e.target.value
      if (value === '') {
        this.mockData = this.mockDataCopy
        return
      }
      const mockDataCopy = JSON.parse(JSON.stringify(this.mockData))
      const level1 = mockDataCopy.children
      for (let i = 0; i < level1.length; i++) {
        const level2 = level1[i].children
        for (let j = 0; j < level2.length; j++) {
          const item = level2[j]
          if (!item.name.includes(value)) {
            level2.splice(j, 1)
            j--
          }
        }
        if (level2.length === 0) {
          level1.splice(i, 1)
          i--
        }
      }
      this.mockData = mockDataCopy
    },
    onChange(item) {
      if (!item.hasOwnProperty('isChecked')) {
        this.$set(item, 'isChecked', false)
      }
      item.isChecked = !item.isChecked
      if (item.isChecked) {
        checked.push(item)
      } else {
        const index = checked.findIndex((one) => one === item)
        checked.splice(index, 1)
      }
    },
    toggle(item) {
      console.log(123, item)
      item.unfolded = !item.unfolded
    },
    handleOk() {
      checked.forEach((item) => {
        delete item.isChecked
      })
      this.$emit('ok', checked)
      this.$emit('update:visible', false)
      checked = []
    },
    handleCancel() {
      this.$emit('update:visible', false)
    },
  },
}
</script>

<style lang="less">
.rDialog-add {
  .rd-h4 {
    margin-top: -0.5em;

    em {
      color: #5498c0;
      font-style: normal;
      padding: 0 5px;
    }
  }

  .rd-selectArea {
    min-height: calc(100% - 21px);
    padding: 15px;
    background-color: #fafafa;

    ul {
      list-style: none;
      padding-left: 0;
      margin: 5px 0;
      font-size: 12px;

      li {
        margin: 10px 0;
      }

      .anticon-minus-circle,
      .anticon-plus-circle {
        color: #455986;
        font-size: 14px;
        margin-right: 12px;
      }
    }

    .rd-h5 {
      margin-top: 10px;
      margin-bottom: 20px;
    }

    .rd-h6 {
      margin-bottom: 10px;
      position: relative;

      &:before {
        content: '';
        position: absolute;
        top: 50%;
        left: -14px;
        width: 14px;
        height: 1px;
        background-color: #455986;
      }
    }

    .rd-ul-1 {
      position: relative;

      &:before {
        content: '';
        position: absolute;
        top: -15px;
        width: 10px;
        height: 2px;
        background-color: #455986;
      }

      &:after {
        content: '';
        position: absolute;
        top: -15px;
        bottom: 0;
        left: 5px;
        width: 1px;
        background-color: #455986;
      }

      .rd-li-1 {
        margin-left: 20px;
      }
    }

    .rd-ul-2 {
      position: relative;

      &:before {
        content: '';
        position: absolute;
        top: -5px;
        left: 27px;
        width: 10px;
        height: 2px;
        background-color: #455986;
      }

      &:after {
        content: '';
        position: absolute;
        top: -5px;
        bottom: 11px;
        left: 32px;
        width: 1px;
        background-color: #455986;
      }

      .rd-li-2 {
        margin-left: 50px;
        display: flex;
        justify-content: space-between;
        position: relative;

        &:before {
          content: '';
          position: absolute;
          top: 9px;
          left: -17px;
          width: 14px;
          height: 1px;
          background-color: #455986;
        }
      }
    }

    .rd-empty {
      margin: 15px 0;
      text-align: center;
    }
  }
}
</style>